import React, { useState } from 'react';
import { useSession } from 'next-auth/react';
import { loadStripe } from '@stripe/stripe-js';
import { Elements } from '@stripe/react-stripe-js';
import CheckoutForm from './CheckoutForm';

const stripePromise = loadStripe(process.env.NEXT_PUBLIC_STRIPE_PUBLISHABLE_KEY);

export const plans = [
    {
        priceId: process.env.NEXT_PUBLIC_STRIPE_PRICE_ID_MONTHLY,
        price: 39.99,
        duration: '/month'
    },
    {
        priceId: process.env.NEXT_PUBLIC_STRIPE_PRICE_ID_YEARLY,
        price: 399.00,
        duration: '/year'
    }
];

const Pricing = () => {
    const { data: session } = useSession();
    const [plan, setPlan] = useState(plans[0]);

    return (
        <section id="pricing">
            <div className="py-24 px-8 max-w-5xl mx-auto">
                <div className="flex flex-col text-center w-full mb-20">
                    <p className="font-medium text-primary mb-5">Pricing</p>
                    <h2 className="font-bold text-3xl lg:text-5xl tracking-tight">
                        Subscription Plans
                    </h2>
                </div>

                <div className="relative flex justify-center flex-col lg:flex-row items-center lg:items-stretch gap-8">
                    <div className="w-full max-w-lg">
                        <div className="relative flex flex-col h-full gap-5 lg:gap-8 z-10 bg-base-100 p-8 rounded-xl">
                            <div className="flex items-center gap-8">
                                <div
                                    className="flex items-center gap-2"
                                    onClick={() => setPlan(plans[0])}
                                >
                                    <input
                                        type="radio"
                                        name="monthly"
                                        className="radio"
                                        checked={plan.price === 39.99}
                                    />
                                    <span>Pay monthly</span>
                                </div>
                                <div
                                    className="flex items-center gap-2"
                                    onClick={() => setPlan(plans[1])}
                                >
                                    <input
                                        type="radio"
                                        name="yearly"
                                        className="radio"
                                        checked={plan.price === 399.00}
                                    />
                                    <span>Pay yearly (60% OFF )</span>
                                </div>
                            </div>

                            <div className="flex gap-2">
                                <p className="text-5xl tracking-tight font-extrabold">
                                    ${plan.price}
                                </p>
                                <div className="flex flex-col justify-end mb-[4px]">
                                    <p className="text-sm tracking-wide text-base-content/80 uppercase font-semibold">
                                        {plan.duration}
                                    </p>
                                </div>
                            </div>

                            <div className="space-y-2">
                                <Elements stripe={stripePromise}>
                                    <CheckoutForm priceId={plan.priceId} session={session} />
                                </Elements>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Pricing;
